@keyframes spin {
  100% {
    transform: rotate(360deg);
  }
}
.preloader {
  display: none;
  width: 100%;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  margin: auto;
  padding: 80px 0;
  background-color: #f5f6f7;
}

.preloader_active {
  display: block;
}

.preloader__circle {
  display: flex;
  width: 74px;
  height: 74px;
  align-items: center;
  position: relative;
  box-sizing: border-box;
  margin: auto;
  animation: spin 1s infinite linear;
  border-radius: 50%;
  background: linear-gradient(#1a1b22, #d1d2d6);
}
.preloader__circle::before {
  content: '';
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  border-radius: 50%;
  margin: 1px;
  background: #f5f6f7;
}

.preloader__title {
  font-size: 24px;
  line-height: 24px;
  text-align: center;
  color: #b6bcbf;
  margin: 24px 0 0;
  font-weight: 400;
}
